"use client"

import { useState, useEffect } from "react"
import { DashboardLayout } from "@/components/layout/dashboard-layout"
import { PricingTable } from "@/components/pricing/pricing-table"
import { supabase } from "@/lib/supabase"
import type { Pricing } from "@/lib/types"
import { Loader2, Plus } from "lucide-react"
import { Button } from "@/components/ui/button"
import { toast } from "sonner"

export default function PricingPage() {
    const [pricing, setPricing] = useState<Pricing[]>([])
    const [loading, setLoading] = useState(true)

    const fetchPricing = async () => {
        try {
            const { data, error } = await supabase
                .from('pricing')
                .select('*')
                .order('activity_name', { ascending: true })

            if (error) throw error
            setPricing(data || [])
        } catch (error) {
            console.error('Error fetching pricing:', error)
            toast.error('Failed to fetch pricing data')
        } finally {
            setLoading(false)
        }
    }

    useEffect(() => {
        fetchPricing()
    }, [])

    if (loading) {
        return (
            <DashboardLayout>
                <div className="min-h-screen flex items-center justify-center">
                    <Loader2 className="w-8 h-8 animate-spin text-[#C19B76]" />
                </div>
            </DashboardLayout>
        )
    }

    return (
        <DashboardLayout>
            <div className="p-4 lg:p-8 space-y-6">
                {/* Header */}
                <div className="flex items-center justify-between">
                    <div>
                        <h1 className="text-2xl sm:text-3xl lg:text-4xl font-semibold tracking-tight">
                            Pricing Management
                        </h1>
                        <p className="text-muted-foreground mt-1 text-sm sm:text-base">
                            Control activity pricing across Marragafay. Changes reflect instantly on the main site.
                        </p>
                    </div>
                    <Button
                        onClick={() => {
                            // This will be handled by the table component
                            const addButton = document.querySelector('[data-action="add-pricing"]') as HTMLButtonElement
                            addButton?.click()
                        }}
                        className="bg-[#C19B76] hover:bg-[#A17D5F] text-white"
                    >
                        <Plus className="w-4 h-4 mr-2" />
                        Add Activity
                    </Button>
                </div>

                {/* Pricing Table */}
                <PricingTable pricing={pricing} onRefresh={fetchPricing} />
            </div>
        </DashboardLayout>
    )
}
